import LC from 'leancloud-storage';  //  leancloud 


// const TABLE_NAME = 'Category';  // ()
class DataService {
    constructor(table_name) {
        this.TABLE_NAME = table_name;
    }
   /**
    * 
    * @param {*} include  Class ["category", "createdBy", "lastEditor"]
    * @param {*} field createdAt Desc
    * @returns 
    */
    fetchAll(include = [''], field = 'createdAt') {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            //  include()   MySQL  join 
            query.include(include)
                //  
                .descending(field)
                .find()
                .then(resp => {
                    console.log('fetchAll():', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('fetchAll():', error);
                    reject(error);
                });
        });
    }

    /**
     * 
     * @param {Object} data :  
     * @returns 
     */
    create(data) {
        let Collection = LC.Object.extend(this.TABLE_NAME);
        let query = new Collection(data);
        return new Promise((resolve, reject) => {

            query.save()
                .then(resp => {
                    console.log('create(data):', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('create(data):', error);
                    reject(error);
                });
        });
    }

    /**
     * 
     * @param {*} id  id  
     * @returns 
     */
    getDataById(id) {
        return new Promise((resolve, reject) => {
            let query = new LC.Query(this.TABLE_NAME);
            query.get(id)
                .then(resp => {
                    console.log('getDataById(id): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('getDataById(id): ', error);
                    reject(error);
                });
        });
    }

    /**
     * 
     * @param {*} id  id
     * @returns 
     */
    delete(id) {
        console.log('delete by Id:', id);
        return new Promise((resolve, reject) => {
            let query = LC.Object.createWithoutData(this.TABLE_NAME, id);
            query.destroy()
                .then(resp => {
                    console.log('delete(id):', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('delete(id):', error);
                    reject(error);
                });
        });
    }

    /**
     * 
     * @param {*} id  id
     * @param {*} data  id  , 
     * @returns 
     */
    update(id, data) {
        console.log('update data:', data);
        return new Promise((resolve, reject) => {
            //  id 
            let query = LC.Object.createWithoutData(this.TABLE_NAME, id);
            query.set(data);  // 
            query.save()
                .then(resp => {
                    console.log('update(id, data): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('update(id, data): ', error);
                    reject(error);
                });
        });
    }

    /**
     * id destroyAll() 
     * 
     * @param {*} items :
     * @returns 
     */
    deleteAll(items) {
        //  id 
        let ids = items.map(item => { return item.id; });
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            //  id 
            query.containedIn('objectId', ids)
                .destroyAll() // 
                .then(resp => {
                    console.log('deleteAll(items): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('deleteAll(items): ', error);
                    reject(error);
                });
        });
    }


    /**
     *  id 
     * @param {*} ids  id  
     */
    findItemsByIds(ids) {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            query.containedIn('objectId', ids)
                .then(resp => {
                    console.log('findItemsByIds(ids): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('findItemsByIds(ids): ', error);
                    reject(error);
                });
        });
    }


    /**
     * 
     * @param {*} items :
     * @param {*} column :
     * @param {*} val : 
     * @returns 
     */
    batchUpdate(items, column, val) {
        let ids = items.map(item => { return item.id; });
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            query.containedIn('objectId', ids)
                .find()
                .then(resp => {
                    console.log(' batchUpdate(items, column, val): ', resp);
                    return resp.map(item => {
                        item.set(column, val);  //   
                        console.log(item);
                        return item;
                    });
                })
                .then(result => {
                    // 
                    LC.Object.saveAll(result)
                        .then(level => {
                            resolve(level);
                        });
                })
                .catch(error => {
                    console.log('updateALL(items): ', error);
                    reject(error);
                });
        });
    }


    /**
     * 
     * @param {*} title 
     * @returns true  false
     */
    exists(title) {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            query.equalTo('title', title);
            query.count()
                .then(count => {
                    console.log('exists(title):', count);
                    if (count) {
                        resolve(true);
                    } else {
                        resolve(false);
                    }
                })
                .catch(error => {
                    console.log('exists(title):', error.code);
                    if (error.code == '101') {
                        resolve(false);
                    } else {
                        reject(false);
                    }
                });
        });
    }

    // 
    currentUser() {
        return LC.User.current();
    }



}

// 
export default DataService;