import DataService from './data_service';
import LC, { Query } from 'leancloud-storage';  //  leancloud 

let TABLE = 'Topic';

class TopicService extends DataService {
    constructor(table_name) {
        super(table_name);
        this.TABLE_NAME = table_name;
    }

    getTopics(id, limit = 5, skip = 0) {
        console.log('id: ', id);
        //  'Category'  leancloud  Class , C
        const c = LC.Object.createWithoutData('Category', id);
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {
            //  include()   MySQL  join 
            //  'category'  Topic , c 
            query.equalTo('category', c)
                .include('createdBy', 'lastEditor')
                //  
                .descending('level')
                .addDescending('createdAt')
                .limit(limit)
                .skip(skip)
                .find()
                .then(resp => {
                    console.log('fetchAll():', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log('fetchAll():', error);
                    reject(error);
                });
        });
    }

    /**
     * id
     * @param {*} id 
     * @returns 
     */
    getTopicById(id) {
        let query = new LC.Query(this.TABLE_NAME);
        return new Promise((resolve, reject) => {

            query.include('createdBy', 'lastEditor', "category")
                // id 
                .get(id)
                .then(resp => {
                    console.log(' getTopicById(id): ', resp);
                    resolve(resp);
                })
                .catch(error => {
                    console.log(' getTopicById(id): ', error);
                    reject(error);
                });
        });

    }

}



// 
export default new TopicService(TABLE);