<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="topics"
      :search="search"
      v-model="selected"
      show-select
      class="elevation-2 ma-2"
      :loading="loading"
      loading-text="Loading... Please wait"
    >
      <!--  -->
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title></v-toolbar-title>
          <v-divider class="mx-4" inset vertical>
            <!-- vertical  -->
          </v-divider>
          <v-spacer></v-spacer>
          <!--  -->
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            class="mx-4"
          ></v-text-field>
          <!--     start -->
          <v-dialog v-model="dialog" :fullscreen="fullscreen" max-width="900px">
            <!-- dialog  template  -->
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="success" dark class="mb-2" v-bind="attrs" v-on="on">
                <v-icon>mdi-plus</v-icon>
                New Item
              </v-btn>
            </template>
            <!-- dialog  </template>  -->
            <v-card>
              <!--      -->
              <v-toolbar dark class="primary white--text">
                <v-tooltip bottom v-if="fullscreen">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      v-bind="attrs"
                      v-on="on"
                      icon
                      dark
                      @click="fullscreen = false"
                    >
                      <v-icon>mdi-vector-arrange-below</v-icon>
                    </v-btn>
                  </template>
                  <span></span>
                </v-tooltip>

                <v-tooltip bottom v-else>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      v-bind="attrs"
                      v-on="on"
                      icon
                      dark
                      @click="fullscreen = true"
                    >
                      <v-icon> mdi-square-outline</v-icon>
                    </v-btn>
                  </template>
                  <span></span>
                </v-tooltip>

                <!-- 
                <v-btn title="" v-else icon dark @click="fullscreen = true">
                  <v-icon> mdi-square-outline</v-icon>
                </v-btn> -->

                <span class="text-h5 mx-4">{{ formTitle }}</span>

                <v-spacer></v-spacer>
                <v-btn class="white--text mx-4" text @click="close">
                  Cancel
                </v-btn>
                <v-btn class="success darken-1" @click="save"> Save </v-btn>
              </v-toolbar>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col :cols="formCols">
                      <v-select
                        :items="items"
                        v-model="editedItem.category"
                        label=""
                      ></v-select>
                    </v-col>
                    <v-col cols="2" v-if="editedIndex > -1">
                      <!--  level  type="number" -->
                      <v-text-field
                        v-model="editedItem.level"
                        label="Level"
                        value="0"
                        type="number"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <!-- Title -->
                      <v-text-field
                        v-model="editedItem.title"
                        label="Topic Title"
                        :counter="70"
                        clearable
                      ></v-text-field>
                    </v-col>
                    <!-- </v-row>
                  <v-row> -->
                    <v-col cols="12">
                      <span> Markdown </span>
                      <!--  -->
                      <v-textarea
                        :rows="rows"
                        v-model="editedItem.content"
                        label="Topic Content"
                        counter
                        outlined
                      ></v-textarea>
                    </v-col>
                    <v-col cols="12">
                      <span> </span>
                      <!-- Title -->
                      <v-text-field
                        v-model="editedItem.tags"
                        label="Tags"
                        :counter="50"
                        clearable
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-card>
          </v-dialog>
          <!--     end -->
        </v-toolbar>
      </template>
      <!--   -->

      <!-- /  -->
      <template v-slot:[`item.count`]="{ item }">
        {{ item.views }}/{{ item.reply }}
      </template>
      <!-- /   -->
      <!-- actions  -->
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          dense
          class="mr-2"
          color="amber"
          @click="editItem(item)"
          title="Edit Item"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          color="red darken-2"
          dense
          @click="deleteItem(item)"
          title="Delete Item"
        >
          mdi-delete
        </v-icon>
      </template>
      <!-- actions   -->
    </v-data-table>
  </v-container>
</template>

<script>
import moment from "moment"; //
import CategoryService from "@/service/category_service.js";
import TopicService from "@/service/topic_service.js";
export default {
  name: "topic-manager",
  data: () => ({
    headers: [
      {
        text: "Category",
        value: "category.text",
        sortable: true,
        align: "start",
      },
      { text: "Title", value: "title", sortable: true, align: "start" },
      {
        text: "LastEditor",
        value: "lastEditor.username",
        sortable: true,
        width: 120,
      },
      {
        text: "Level",
        value: "level",
        sortable: true,
        width: 80,
        align: "center",
      },
      {
        text: "Views/Reply",
        value: "count",
        sortable: false,
        width: 90,
        align: "center",
      },
      { text: "CreatedAt", value: "createdAt", sortable: true, width: 130 },
      { text: "UpdatedAt", value: "updatedAt", sortable: true, width: 130 },
      {
        text: "Actions",
        value: "actions",
        sortable: false,
        width: 80,
        align: "center",
      },
    ],
    items: [], // 
    categories: [], // 
    topics: [], // 
    selected: [], //
    search: "", // 
    editedIndex: -1, //     -1 categories
    // 
    editedItem: {
      title: "",
      category: "",
      content: "",
      level: 0,
      tags: "",
    },
    // 
    defaultItem: {
      title: "",
      category: "",
      content: "",
      level: 0,
      tags: "",
    },
    currentCategory: "",
    fullscreen: false,
    loading: false, // 
    dialog: false, //     
    dialogDelete: false, //   
  }),
  mounted() {
    moment.locale("zh-cn"); // 
    this.getCategory();
    this.getTopics();
  },
  methods: {
    getCategory() {
      this.loading = true; // 
      this.items = [];
      CategoryService.fetchAll("level")
        .then((resp) => {
          if (resp.length > 0) {
            this.items = resp.map((item) => {
              return {
                text: item.get("title"),
                value: item.id,
              };
            });
            //    
            this.categories = resp;
            this.loading = false; // 
          }
        })
        .catch((error) => {
          this.loading = false; // 
        });
    },

    getTopics() {
      TopicService.fetchAll(["category", "createdBy", "lastEditor"])
        .then((resp) => {
          this.topics = resp.map((item) => this.formJson(item));
        })
        .catch((error) => {});
    },

    save() {
      if (this.editedIndex > -1) {
        // 
        //  updatedAtleancloudupdatedAt 
        this.editedItem.updatedAt = moment(Date.now()).format("lll");
        // 
        this.editedItem.lastEditor = this.getUser(TopicService.currentUser());
        // 
        let item = Object.assign(
          this.topics[this.editedIndex],
          this.editedItem
        );
        // 
        let categoryId = this.editedItem.category.value;
        var category = this.categories.find((item) => item.id === categoryId);

        TopicService.update(item.id, {
          category: category,
          title: item.title,
          content: item.content,
          level: Number(item.level), // 
          lastEditor: TopicService.currentUser(), // 
          tags: item.tags,
        });
      } else {
        //   count 
        let categoryId = this.editedItem.category;
        var category = this.categories.find((item) => item.id === categoryId);
        if (categoryId != "") {
          let topic = {
            category: category,
            title: this.editedItem.title.trim(),
            content: this.editedItem.content,
            level: 0,
            views: 0,
            reply: 0,
            createdBy: TopicService.currentUser(), // 
            lastEditor: TopicService.currentUser(), // 
            tags: this.editedItem.tags,
          };

          console.log(topic);

          if (topic.title != "" && topic.content != "") {
            TopicService.create(topic)
              .then((resp) => {
                console.log("ok");
                this.topics.unshift(this.formJson(resp));
              })
              .catch((error) => {});
          }
        }
      }
      this.close();
    },

    editItem(item) {
      console.log("editItems: ", this.items);
      //  categories 
      this.editedIndex = this.topics.indexOf(item);
      this.editedItem = Object.assign({}, item);
      console.log("editItem: ", this.editedItem);
      // // 
      // this.currentCategory = item.get("category").get("title");
      this.dialog = true;
    },

    deleteItem(item) {
      //
    },

    formJson(item) {
      return {
        id: item.id,
        category: item.get("category")
          ? {
              // 
              text: item.get("category").get("title"),
              value: item.get("category").id,
            }
          : "",
        title: item.get("title"),
        content: item.get("content"),
        level: item.get("level"),
        views: item.get("views"),
        reply: item.get("reply"),
        tags: item.get("tags"),
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
      };
    },

    // 
    close() {
      this.dialog = false; // 
      // this.$nextTick() DOM 
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
  },

  // 
  computed: {
    // 
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    //      Title 
    //  LevelTitle  cols="12"
    //  LevelTitle  cols="10"
    formCols() {
      return this.editedIndex === -1 ? 12 : 10;
    },
    //  12  6 
    rows() {
      return this.fullscreen ? 12 : 6;
    },
  },

  // 
  watch: {
    //  data dialogval 
    dialog(val) {
      val || this.close(); // 
    },

    dialogDelete(val) {
      val || this.close(); // 
    },
  },
};
</script>

<style>
</style>